.jury__title {
  margin: 0 0 45px 0;

  font-weight: 600;
  font-size: 54px;
  line-height: 54px;
  font-family: $ff-alumni;
  color: $color-sapphire;
  letter-spacing: 1.62px;

  @include vp-1365 {
    margin: 0 0 40px 0;

    font-size: 42px;
    line-height: 42px;
    text-align: center;
  }
  @include vp-767 {
    margin: 0 0 42px 0;

    font-size: 32px;
    line-height: 32px;
  }
}

.jury__wrapper {
  position: relative;
  width: 1160px;
}

.jury__card {
  position: relative;

  width: 260px;
  height: 294px;

  text-align: center;
  color: $color-default-white;

  figure {
    width: 100%;
    height: 100%;
    margin: 0;
  }

  figcaption {
    position: absolute;
    bottom: 26px;
    left: 50%;

    font-size: 22px;
    line-height: 22px;

    transform: translateX(-50%);
  }

  &:hover {
    .jury__info {
      z-index: 1;
    }
  }
}

.jury__swiper-wrapper {
  display: flex;
}

.jury__info {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;

  width: 100%;
  height: 100%;
  padding: 45px 40px 50px;

  background-color: rgba($color-sapphire, 0.89);

  h3 {
    margin: 0 0 8px 0;

    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
  }

  p {
    margin: 0 0 33px 0;

    font-weight: 300;
    font-size: 16px;
    line-height: 24px;
  }

  ul {
    margin: 0;
    padding: 0 0 0 20px;

    font-size: 14px;
    line-height: 22px;
    text-align: left;
  }

  li + li {
    margin-top: 12px;
  }
}

.jury__button {
  height: 56px;
  width: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  outline: 1px solid $color-default-black;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -76px;

  &--next {
    left: unset;
    right: -76px;

    svg {
      transform: rotate(180deg);
    }
  }

  svg {
    width: 16px;
    height: 32px;
  }
}
